@import "styles/variables";
@import "styles/mixins";

.footer {
  position: relative;
  background-color: $colorDarkest;
}

.wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 6rem;
  padding-bottom: 6rem;
}

.item {
  &:not(:first-child) {
    margin-top: 0.5rem;
  }
}

.button {
  @include resetButton;
  @include step($colorMediumDark, 400, "Roboto");

  text-decoration: underline;
}

.copy {
  @include step($colorMediumDark, 400, "Roboto");
}

.logo {
  width: 8rem;
  fill: $colorMediumDark;
}

.mascot {
  position: absolute;
  top: -19rem;
  left: 20rem;
  z-index: 2;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    display: grid;
    grid-template-columns: 1fr 8rem;
    grid-column-gap: 1rem;
    align-items: flex-start;
    padding-top: 3.5rem;
    padding-bottom: 2.5rem;
  }

  .list,
  .copy {
    grid-column: 1 / 2;
  }

  .list {
    @include resetList;

    .item {
      &:not(:first-child) {
        margin-top: 0.8rem;
      }
    }
  }

  .logo {
    grid-row: 1  / 4;
    grid-column: 2 / 3;
    margin-top: 1rem;
  }

  .copy {
    margin-top: 1rem;
  }

  .mascot {
    top: -9.5rem;
    left: 6.3rem;
  }
}
