@import "styles/variables";
@import "styles/mixins";

.list {
  @include resetList;

  margin-top: 2rem;
}

.listSpaced {
  .item {
    &:not(:first-child) {
      margin-top: 1rem;
    }
  }
}

.item {
  @include listItem;

  &:not(:first-child) {
    margin-top: 0.1rem;
  }
}

.listNumbered {
  counter-reset: index;

  .item {
    @include regular($colorLightest, 500);

    padding-top: 6rem;
    padding-left: 7rem;

    & > div {
      position: relative;
    }

    &::before {
      @include counter;

      --counter: counter(index);
      counter-increment: index;
      content: counter(index);
      position: absolute;
      top: 0;
      left: 0;
      width: auto;
      height: auto;
      background-color: transparent;
      border-radius: 0;
      opacity: 0.2;
      z-index: 0;
    }

    &:nth-child(2n) {
      &::before {
        left: 1.5rem;
      }
    }
  }
}

.listSmall {
  .item {
    @include step($colorDarkest, 400, "Roboto");
  }
}

@media (max-width: $adaptiveMax) {
  .list.listNumbered {
    margin-left: auto;
    margin-right: auto;
    width: calc(36rem - 2 * var(--padding-global-h));

    .item {
      padding-top: 3rem;
      font-weight: 400;

      &::before {
        top: 2rem;
      }

      &:nth-child(2n + 1) {
        padding-left: 17.5rem;

        &::before {
          left: auto;
          right: 17.5rem;
        }

      }

      &:nth-child(2n) {
        padding-right: 18.5rem;
        padding-left: 0;
        text-align: right;

        &::before {
          top: 0;
          right: auto;
          left: 17.5rem
        }
      }
    }
  }

  .listSmall {
    .item {
      font-size: 1.4rem;
      line-height: 1.2;
    }
  }
}
