@import "styles/variables";
@import "styles/mixins";

.wrapper {
  position: relative;
  padding-top: 10rem;
  padding-bottom: 12rem;
  overflow: hidden;
  border-radius: var(--section-border-radius);
}

.intro {
  position: relative;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: 6.5rem;
  align-items: center;
  z-index: 1;
}

.heading {
  text-align: right;
}

.description {
  padding-right: 30%;
}

.list {
  position: relative;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 3rem;
  margin-top: 10rem;
  z-index: 1;
}

.button {
  position: relative;
  display: block;
  margin: 4.5rem auto 0 auto;
  z-index: 1;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding-top: 5rem;
    padding-bottom: 7rem;
  }

  .intro {
    display: block;
    grid-column-gap: 0;
  }

  .heading {
    text-align: left;
  }

  .description {
    margin-top: 0.8rem;
    padding-right: 5rem;
  }

  .frame {
    @include hideScrollbar;

    margin-left: calc(-1 * var(--padding-global-h));
    width: calc(100% + 2 * var(--padding-global-h));
    overflow: scroll;
  }

  .list {
    grid-template-columns: repeat(3, 27rem);
    margin-top: 4rem;
    padding: 0 var(--padding-global-h);
    width: max-content;
  }

  .button {
    margin: 3rem auto 0 auto;
    font-size: 1.4rem;
    line-height: 1.6rem;
    border: none;
    box-shadow:
      inset 0 10rem 8rem 0 rgba(255, 52, 18, 0.07),
      inset 0 4.1rem 3.3rem 0 rgba(255, 52, 18, 0.05),
      inset 0 2.2rem 1.7rem 0 rgba(255, 52, 18, 0.04);

    &:active {
      border: 2px solid white;
    }
  }
}
