@import "../../../styles/variables";
@import "../../../styles/mixins";

.section {
  position: relative;
}

.wrapper {
  position: relative;
  padding-top: 12rem;
  padding-bottom: 12rem;
}

.heading {
  color: $colorDarkest;
  text-align: center;
}

.list {
  @include resetList;

  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 25rem;
  margin-top: 5rem;
  padding-right: 9rem;
  padding-left: 13rem;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding-top: 5rem;
    padding-bottom: 6.7rem;
  }

  .heading {
    padding-right: 10%;
    text-align: left;

    span {
      color: $colorHighlight;
    }
  }

  .list {
    display: flex;
    flex-direction: column;
    grid-gap: 0;
    margin-top: 0;
    padding-right: 0;
    padding-left: 0;
  }

  .item {
    &:not(:first-child) {
      margin-top: 2rem;
    }
  }
}
