@import "styles/variables";
@import "styles/mixins";

.wrapper {
  display: grid;
  grid-template-columns: 4.6rem 1fr;
  grid-column-gap: 1.8rem;
}

.description {
  @include nav($colorDarkest, 400);

  padding-top: 0.7rem;
  font-size: 1.2rem;
  line-height: 1.4rem;
  color: $colorDarkest;

  ul {
    list-style: inside;
  }
}
