@import "styles/variables";
@import "styles/mixins";

.wrapper {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 56rem;
  grid-column-gap: 20rem;
  padding-top: 12rem;
}

.description {
  @include descriptionPromo;

  margin: 2rem 0 0 0;
}

.contacts {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 4rem;
  background-color: $colorHighlight;
  border-radius: 3rem;
}

.label {
  @include descriptionPromo($colorLightest);
}

.phone {
  @include h3($colorLightest);
}

.form  {
  position: relative;
  margin-top: 2rem;
  z-index: 1;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    display: block;
    grid-column-gap: 0;
    padding-top: 5rem;
  }

  .description {
    margin: 0.8rem 0 0 0;
    font-size: 1.4rem;
    line-height: 1.6rem;
  }

  .content {
    margin-top: 2.5rem;
  }

  .contacts {
    padding: 1.7rem 2rem 2rem 2rem;
  }

  .form {
    margin-top: 1.1rem;
  }
}
