@import "styles/mixins";

.wrapper {
  position: relative;
}

.label {
  @include step;

  position: relative;
  display: block;
  margin-left: 1rem;
  padding: 0 0.5rem;
  letter-spacing: 0.2rem;
  text-align: center;
  text-transform: lowercase;
  writing-mode: vertical-rl;
  background-color: white;
  transform: scale(-1, -1);

  &::before {
    content: "";
    position: absolute;
    top: calc(50% - 4.5rem / 2);
    left: -1rem;
    width: 4.5rem;
    height: 4.5rem;
    border: 2px solid $colorHighlight;
    border-radius: 50%;
    z-index: -1;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: white;
    z-index: -1;
  }
}

.icon {
  margin-top: 1rem;
  margin-left: 1.3rem;
}
