@import "styles/mixins";
@import "styles/variables";

.wrapper {
  :global {
    .swiper-slide {
      //padding: 0 2.5rem;
      width: 28rem;

      &:first-child {
        margin-left: 3rem;
      }

      &:last-child {
        margin-right: 3rem;
      }
    }
  }
}

.controls {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
}

.button {
  @include resetButton;

  display: flex;
  align-items: center;
  padding: 0.5rem 1rem;

  &:disabled {
    .icon {
      fill: $colorMediumDark;
      opacity: 0.4;
    }
  }
}

.buttonPrev {
  .icon {
    transform: rotate(180deg);
  }
}

.icon {
  width: 2.4rem;
  fill: $colorDarkest;
}

.counter {
  @include nav($colorDarkest, 400);
}
