@import "styles/variables";
@import "styles/mixins";

.radio {
  @include visuallyHidden;
}

.label {
  @include regular($colorDarkest);

  position: relative;
  display: block;
  padding-left: 5.5rem;
  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 4rem;
    height: 4rem;
    border: 3px solid $colorHighlight;
    border-radius: 50%;
  }

  &::after {
    content: "";
    position: absolute;
    top: 1.1rem;
    left: 1.1rem;
    width: 1.8rem;
    height: 1.8rem;
    background-color: $colorHighlight;
    border-radius: 50%;
    transition-duration: $transitionDuration;
    opacity: 0;
    transform: scale(0);
  }
}

.wrapperChecked {
  .label {
    &::after {
      opacity: 1;
      transform: scale(1);
    }
  }
}

@media (max-width: $adaptiveMax) {
  .label {
    padding-left: 3.5rem;
    font-size: 1rem;
    line-height: 1.2rem;

    &::before {
      width: 2.6rem;
      height: 2.6rem;
      border: 2px solid $colorHighlight;
      border-radius: 50%;
    }

    &::after {
      top: 0.7rem;
      left: 0.73rem;
      width: 1.2rem;
      height: 1.2rem;
    }
  }
}
