@import "styles/variables";
@import "styles/mixins";

.wrapper {
  padding-top: 12rem;
  padding-bottom: 12rem;
}

.heading {
  @include visuallyHidden;
}

.lead {
  @include h2($colorHighlight);

  text-transform: uppercase;
}

.description {
  margin-top: 2rem;
  color: $colorDarkest;
}

.list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 7rem;
  margin-top: 6rem;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding-top: 5rem;
    padding-bottom: 5rem;
    overflow: hidden;
  }

  .frame {
    @include hideScrollbar;

    margin-left: calc(-1 * var(--padding-global-h));
    width: calc(100% + 2 * var(--padding-global-h));
    overflow: scroll;
  }

  .list {
    @include resetList;

    grid-template-columns: repeat(4, 21rem);
    grid-column-gap: 8rem;
    margin-top: 4rem;
    padding-left: var(--padding-global-h);
    width: max-content;
  }
}
