@import "styles/variables";
@import "styles/mixins";

.form {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3rem 8rem 12rem 8rem;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 70%;
    background-color: $colorMedium;
    border-radius: 3rem;
  }

  & > * {
    position: relative;
    z-index: 1;
  }
}

.checked {
  margin-top: 3rem;
}

.button {
  @include descriptionPromo($colorLightest, 400);

  margin-top: 4rem;
  width: 20rem;
  height: 6rem;
}

@media (max-width: $adaptiveMax) {
  .form {
    padding: 1.7rem 1.7rem 15rem 1.7rem;

    &::before {
      height: 53%;
    }
  }

  .input {
    &:not(:first-child) {
      margin-top: 1rem;
    }
  }

  .checked {
    margin-top: 2rem;
    margin-left: 2.8rem;
  }

  .button {
    margin-top: 3rem;
    width: 16rem;
    height: 5rem;
  }
}
