@import "styles/variables";

.wrapper {
  display: grid;
  grid-template-columns: 44rem 1fr;
  grid-column-gap: 3.5rem;
  align-items: center;
  padding-top: 15rem;
  padding-bottom: 15rem;
}

.list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 3rem 8rem;
  margin: 0;
}

.icon {
  display: none;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    position: relative;
    display: block;
    grid-column-gap: 0;
    padding-top: 5rem;
    padding-bottom: 7.5rem;
  }

  .frame {
    position: relative;
  }

  .list {
    display: flex;
    flex-direction: column;
    grid-gap: 0;
  }

  .icon {
    display: block;
    position: absolute;
    top: 0.5rem;
    left: calc(50% - 12rem);
  }
}
